.wrapper{
    width: 100%;
    overflow: hidden;
}
.container{
    width: 1440px;
    height: 100vh;
    margin: 0 auto;
    display: flex;
flex-direction: column;
    align-items: center;
}
.header{
    padding: 20px 0;
    display: flex;
    width: 1200px;
align-items: center;
    justify-content: center;
}
.form__search{
    margin-right: 50px;
}
.search{
    width: 400px;
    height: 30px;
    position: relative;
    z-index: 1;
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
}
.search-icon{
    height: 40px;
    width: auto;
    position: absolute;

}
.checkbox{
    display: flex;
    align-items: center;
    margin-right: 20px;

}
.checkbox p{

    margin-right: 5px;
}

.toggler-wrapper {
    display: block;
    width: 45px;
    height: 25px;
    cursor: pointer;
    position: relative;

}

.toggler-wrapper input[type="checkbox"] {
    display: none;
}

.toggler-wrapper input[type="checkbox"]:checked+.toggler-slider {
    background-color: #44cc66;
}

.toggler-wrapper .toggler-slider {
    background-color: #ccc;
    position: absolute;
    border-radius: 100px;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-transition: all 300ms ease;
    transition: all 300ms ease;

}

.toggler-wrapper .toggler-knob {
    position: absolute;
    -webkit-transition: all 300ms ease;
    transition: all 300ms ease;
}


.toggler-wrapper.style-1 input[type="checkbox"]:checked+.toggler-slider .toggler-knob {
    left: calc(100% - 19px - 3px);
}

.toggler-wrapper.style-1 .toggler-knob {
    width: calc(25px - 6px);
    height: calc(25px - 6px);
    border-radius: 50%;
    left: 3px;
    top: 3px;
    background-color: #fff;
}

/*/////////////////////////////CONTRACT STYLE//////////////////*/
.contract__item{
    width: 400px;
    padding: 20px 10px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 20px;
    background-color: #fff;
    border-radius: 20px;
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
}
.contact__icon{
    height: 50px;
    width: auto;
}

.contact__name{
    margin-top: 5px;
}
.gender__icon{
    height: 20px;
    width: auto;
}
